<template>
    <div class="insure">
        <ul class="con">
            <li>
                <span></span>
                <span style="float:right" v-text="money+''"></span>
            </li>
            <li class="wpay">
                <span></span>
                <div class="wpay-wrap">
                    <input class="wpay-input" type="tel" minlength="1" v-model="wMoney" maxlength="7"  @input='change()'>
                    <img   class="edit-img" src="../../images/edit-money.png" alt="">
                </div>
            </li>
            <li class="zfwrap clearfix" v-on:click.stop.prevent="pickerConfigshow">
                <span style="float:left;"></span>
                <div class="zfSelect">
                    <span class="arrow fr"><img src="../../images/arrowRight.png"></span>
                    <span style="float:right;">{{typePay}}</span>
                </div>
            </li>
        </ul>
        <div class="btnOut">
            <van-button type="danger" size="large" class="btn" v-if="flow == undefined " @click="back"></van-button>
            <!-- <van-button type="danger" size="large" class="btn"  v-if="flow =='L' " @click="back"></van-button> -->
        </div>
        <div id="mask" v-if="maskLoading">
            <img src="../../images/load.gif" />
            <p>
                
            </p>
        </div>
        <picker-rate :pickerConfig="pickerConfig" @getPickerValue="getPickerValue"></picker-rate>
        <password-input :tel="tel" :isSucess="isSucess" :isDisplay="isDisplay" @tranFn="makeSure" @getValue="getValue"></password-input>
        <dialog-page :maskShow="maskShow" @closed="weixinPay" @triggerPa="weixinPay" class="singleBtn"></dialog-page>
    </div>
</template>
<script>
    //totaloverdue0idid-
    import Vue from'vue'
    import { Button ,Toast} from 'vant';
    Vue.component(Button.name, Button);
    import PickerRate from '../../components/PickerRate';
    import PasswordInput from '../../components/PasswordInput'
    import dialogPage from '../../components/LoanDialog'
    import configs from '../../common/commonService'
    const {myFetch ,switchErrorMsg ,preventFn} =configs;
    export default {
        name:'insure',
        data () {
            return {
                isDisplay:false,//
                isSucess:false,
                tel:'',
                maskLoading:false,
                isRen:true,
                flow:this.$route.query.flow,
                isBorrow:true,
                overdue:0,//
                overduePayType:'',//
               /* isGetValue:true,//  order/borrow-repay */
                pickerConfig : {
                    show:false,
                    arrData : [//
                        '','','',''
                    ],

                },
                typePay : '',//
                money : this.$route.query.total,//
                placeHolder: ""+this.$route.query.total+"",
                wMoney : this.$route.query.total,
                id:this.$route.query.id,
                ids:[],
                maskShow:{   //
                    show:false,
                    btnDoub:true,
                    defaultShow:false,
                    /*cancel:'',*/
                    confirmTxt:'',
                    greenFlag:true,
                    overdue:this.$route.query.overdue
                },
            }
        },
        methods : {
            getPickerValue (v) {//
                this.typePay = v;
            },
            pickerConfigshow () {//
                preventFn(this.$el.querySelector('.pickerP'));
                this.pickerConfig.show=true;
            },
            back () { //
                if (!this.isRen) {
                    Toast('');
                    this.$router.push({path:'/certification'});

                }else{  //
                    if(this.overdue > 0){
                        this.maskShow.show = true;//
                    }else{
                         if( parseFloat(this.wMoney) >0){
                            this.isDisplay = true//
                        }else{
                            Toast("");
                        }
                    }
                   
                }
            },
            makeSure () {//
                this.isDisplay = false
                this.isSucess=false;
            },
            //
            weixinPay (type) {

                if(type==''){
                    this.overduePayType =type//
                    this.maskShow.show = false;
                    this.isDisplay = true//
                }else if(type=='') {
                    Toast('')
                }
                else{
                    this.maskShow.show = false;//
                }

            },
            change () {
                /*console.log(parseFloat(this.wMoney));
                console.log(parseFloat(this.money));*/
                this.wMoney = this.wMoney.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');
                if(parseFloat(this.wMoney) > parseFloat(this.money)){
                    Toast("");
                }

            },
            getValue (v) {//
                var homeUrl = '';
                if(window.location.hostname=='prod-iou-h5.xianjinxia.com'){
                    homeUrl = encodeURI(`http://prod-iou-h5.xianjinxia.com/#/payend?id=${this.$route.query.id}`);
                }else{
                    homeUrl = encodeURI(`http://iou-h5.xianjinxia.com/#/payend?id=${this.$route.query.id}`);
                }
                this.psw= v;
                let obj = {
                    id:this.id,
                    repay_type:this.typePay,
                    pwd:v,
                    wMoney:this.wMoney,
                    flow:this.flow

                };
                let _this = this;
                if(v.length==6){

                    if(this.overdue> 0){
                        this.maskLoading = true;
                        let obj = {
                            id:this.id || this.ids,
                            repay_type:this.typePay,
                            password:v,
                            charge_pay_type : "",
                            charge_type:'2',
                            url:homeUrl,
                            flow:this.flow
                        };
                        myFetch(obj,'api/web/pay/overdue-pay',(res)=>{
                            this.maskLoading = false;
                            if(res.code==0){
                                window.location.href = res.data.redirect_url;
                                //Toast('');

                            }else if (res.code=='100012'){
                                Toast(res.message);
                                _this.isDisplay=true;
                            }else if (res.code =='100124') {
                                Toast(res.message);
                                _this.$router.history.push({path:'/trader-psw'})
                            }else{
                                Toast(res.message);
                            }
                        })
                    }else{//
                            if(!_this.isBorrow){
                                return
                            }else{
                                _this.isBorrow = false;
                                setTimeout(()=>{
                                    _this.isDisplay = false;
                                },500);
                                myFetch(obj,'api/web/order/borrow-repay',(res)=>{//
                                    if(res.code=='0'){
                                        /*_this.isSucess = true;*/
                                        let fn = ()=>{
                                            Toast('');
                                            return _this.$router.history.push({path:'/list-pageB/2'})
                                        }
                                        setTimeout(fn,800);
                                        _this.isBorrow =true;

                                    }else if (res.code =='100124') {
                                         _this.isBorrow =true;
                                        Toast(res.message);
                                        _this.$router.history.push({path:'/trader-psw'})
                                    }else if (res.code=='100012'){
                                         _this.isBorrow =true;
                                        Toast(res.message);
                                    }else if (res.code=='100190'){
                                         _this.isBorrow =true;
                                        Toast(res.message);
                                    }else{
                                         _this.isBorrow =true;
                                        Toast(res.message);
                                    }
                                }) 
                            }
                           
                   
                        
                    }

                }

            }
        },
        created () {
            document.title="";
        },
        mounted () {
            let str = this.$route.query.id;
            this.overdue = this.$route.query.overdue;
            if(str.includes('-')){
                this.ids = str.split('-');
            }else{
                this.id = str;
            }

            let _this = this;
            myFetch({},'api/web/user/user-info',res=>{
                if(res.data.id_card ==""){
                    _this.isRen = false;
                }
            })
        },
        components : {
            PickerRate,
            PasswordInput,
            dialogPage,
        }
    }
</script>
<style lang="less" scoped>
    @import './inSure.less';
</style>



